export default {
	name: "uni-navSearchHeader",
	 
	data () {
		return {
			value: ''
		}
	},
	methods: {
		inputChange: function () {
			this.$emit('returnInputDate',this.value)
		},
		goUserClick: function () {
			this.$emit("goUserClick")
		},
		onPickCityClick: function() {
			this.$emit("goPickCityClick")
		}
	}
}

